<template>
<div id="signin">
    <h1 class="mx-3 my-3">Login</h1>
    <div class="signin-form">
            <form @submit.prevent="onSubmit">
        <div class="form-group input">
            <label for="email">Email</label>
            <input type="text" v-model="email" class="form-control" placeholder="Your Email">
        </div>
        <div class="form-group input">
            <label for="password">Password</label>
            <input type="password" v-model="password" class="form-control">
        </div>
        <div class="form-group">
          <p class="error" v-if="isError">Either Email or Password is wrong!</p>
        </div>
        <div class="submit">
            <button type="submit">Login</button>
        </div>
    </form>
    </div>
</div>
</template>

<script>
import AuthService from '../../axios/AuthService'
export default {
    name: 'Login',
    data() {
        return {
            email: '',
            password: '',
            error: ''
        }
    },
    computed: {
      isError() {
        return this.$store.getters.isError;
      }
    },
    methods: {
       async onSubmit () {
            let formdata = {
                email: this.email,
                password: this.password
            };
            let data = JSON.stringify(formdata);
            console.log(data);
            await this.$store.dispatch('login', formdata);
            await this.$store.getters.user;
        }   
    }
}
</script>

<style scoped>
  .signin-form {
    width: 400px;
    margin: 30px auto;
    border: 1px solid #eee;
    padding: 20px;
    box-shadow: 0 2px 3px #ccc;
  }

  .input {
    margin: 10px auto;
  }

  .input label {
    display: block;
    color: #4e4e4e;
    margin-bottom: 6px;
  }

  .input input {
    font: inherit;
    width: 100%;
    padding: 6px 12px;
    box-sizing: border-box;
    border: 1px solid #ccc;
  }

  .input input:focus {
    outline: none;
    border: 1px solid #521751;
    background-color: #eee;
  }

  .submit button {
    border: 1px solid #521751;
    color: #521751;
    padding: 10px 20px;
    font: inherit;
    cursor: pointer;
  }

  .submit button:hover,
  .submit button:active {
    background-color: #521751;
    color: white;
  }

  .submit button[disabled],
  .submit button[disabled]:hover,
  .submit button[disabled]:active {
    border: 1px solid #ccc;
    background-color: transparent;
    color: #ccc;
    cursor: not-allowed;
  }
</style>