<template>
<nav class="navbar navbar-expand-lg navbar-light bg-light">
  <a class="navbar-brand" href="/">Chatbox</a>
  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>

  <div class="collapse navbar-collapse" id="navbarSupportedContent">
    <ul class="navbar-nav mr-auto">
      <li class="nav-item active">
        <a class="nav-link" href="home">Home <span class="sr-only">(current)</span></a>
      </li>
      <li class="nav-item" v-if="auth">
        <a class="nav-link" href="whisper" tabindex="-1" aria-disabled="true">Start Whisper</a>
      </li>
    </ul>
    <div class="justify-content-right">
          <ul class="navbar-nav mr-auto">
    <li class="nav-item" v-if="!auth">
      <router-link to="/login"><button class="btn btn-primary mx-2">Login</button></router-link>
    </li>
    <li class="nav-item" v-if="!auth">
      <router-link to="/registration"><button class="btn btn-primary mx-2">Registration</button></router-link>
    </li>
    <li class="nav-item" v-if="user">
      <p>{{user}}</p>
    </li>
    <li class="nav-item" v-if="auth">
      <button @click="logout" class="btn btn-primary mx-2">Logout</button>
    </li>
    </ul>
    </div>
  </div>
</nav>
</template>

<script>
import AuthService from '../axios/AuthService'
export default {
  name: 'Navbar',
  computed: {
    auth() {
      return this.$store.getters.isAuthenticated;
    },
    user() {
      return this.$store.getters.isAuthenticated ? this.$store.getters.user : false;
    }
  },
  methods: {
    async logout () {
      await this.$store.dispatch('logout');
    }
  },
}
</script>

