<template>
  <div class="row">
    <div class="col-md-2">
      <div class="chat__sidebar">
        <h3>Users (Online)</h3>
        <div id="users"></div>
      </div>
    </div>
    <div class="col-md-10">
      <div class="chat__main">
        <ol id="messages" class="chat__messages" v-chat-scroll="{always: false, smooth: true}">
            <li class="message d-flex align-items-end" v-for="(message, index) in messages" :key="index">
                <p> <strong>{{message.from}}</strong> <span>{{message.createdAt}}</span> : {{message.text}}</p>
            </li>
        </ol>

        <div class="chat__footer">
          <form id="message-form">
            <input name="message" v-model="text" type="text" placeholder="Message" autofocus autocomplete="off" />
            <button type="submit" @click.prevent="onSubmit">Send</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import io from 'socket.io-client'
export default {
    data() {
        return {
            socket: io('http://localhost:3000'),
            text: '',
            messages: this.$store.getters.getMessages
        }
    },
    created() {
        return this.joinServer();
    },
    methods: {
        joinServer() {
            this.socket.on('connect', () => {
                console.log('connected to the server!!!');
                this.socket.on('newText', (data) => {
                    console.log(data);
                    console.log(this.$store.getters.getMessages.length);
                    
                    this.$store.dispatch('setText', data);
                });
            })
        },
        async onSubmit() {
            let message = {
                from: localStorage.getItem('username'),
                text: this.text
            }
            await this.socket.emit('sendText', message);
            this.text = '';
        }   
    }
}
</script>

