import Vue from 'vue'
import Vuex from 'vuex'
import io from 'socket.io-client'
import router from '../router/index'
import AuthService from '../axios/AuthService'
import { stat } from 'fs'

Vue.use(Vuex)
const socket = io('http://localhost:3000');

const token = {
  headers: {
    'x-auth': ''
  }
};

export default new Vuex.Store({
  state: {
    idToken: null,
    userID: null,
    userEmail: null,
    username: null,
    messages: [],
    error: null
  },
  mutations: {
    authUser(state, userData) {
      state.idToken = userData.token,
        state.userID = userData.userID,
        state.userEmail = userData.userEmail,
        state.username = userData.username
    },
    clearAuthData(state) {
      state.idToken = null,
        state.userID = null,
        state.userEmail = null
      state.username = null
    },
    takeError(state, err) {
      state.error = err;
    },
    takeMessage(state, texts) {
      state.messages.push(texts);
    }
  },
  actions: {
    registration({ commit, dispatch }, data) {
      console.log(data);
      AuthService.createUser(data).then((res) => {
        console.log(res);
        console.log(res.data);
        console.log(res.headers["x-auth"]);
        token.headers["x-auth"] = res.headers["x-auth"];
        console.log(token.headers["x-auth"]);
        commit('authUser', {
          token: res.headers["x-auth"],
          userID: res.data._id,
          userEmail: res.data.email,
          username: res.data.username
        });
        localStorage.setItem('token', res.headers["x-auth"])
        localStorage.setItem('userID', res.data._id)
        localStorage.setItem('username', res.data.username)
        router.replace('/');
      }).catch((e) => {
        console.log(e);
      });
    },
    login({ commit, dispatch }, data) {
      console.log(data);
      AuthService.userLogin(data).then((res) => {
        console.log(res);
        console.log(res.headers["x-auth"]);
        token.headers["x-auth"] = res.headers["x-auth"];
        console.log(token.headers["x-auth"]);
        commit('authUser', {
          token: res.headers["x-auth"],
          userID: res.data._id,
          userEmail: res.data.email,
          username: res.data.username
        });
        localStorage.setItem('token', res.headers["x-auth"])
        localStorage.setItem('userID', res.data._id)
        localStorage.setItem('username', res.data.username)
        router.replace('/');
      }).catch((e) => {
        console.log(e);
        commit('takeError', {
          error: e
        });
      });
    },
    logout({ commit }) {
      AuthService.userLogout(token).then((res) => {
        console.log(res);
        commit('clearAuthData');
        token.headers["x-auth"] = '';
        localStorage.removeItem('token')
        localStorage.removeItem('userID')
        localStorage.removeItem('username')
        router.replace('/login')
      }).catch((e) => {
        console.log(e);
      });
    },
    tryAutoLogin({ commit }) {
      let dataToken = localStorage.getItem('token');
      if (!token) {
        return
      }
      token.headers["x-auth"] = dataToken;
      let dataUserID = localStorage.getItem('userID');
      let dataUsername = localStorage.getItem('username');
      commit('authUser', {
        token: dataToken,
        userID: dataUserID,
        username: dataUsername
      });
    },
    //chatting
    setText({ commit }, data) {
          commit('takeMessage', data);
    }
  },
  getters: {
    user(state) {
      console.log(state.username)
      return state.username;
    },
    isAuthenticated(state) {
      console.log('line 107: ', state.idToken);

      return state.idToken != null;
    },
    isError(state) {
      return state.error != null;
    },
    getMessages(state) {
      return state.messages;
    }
  }
})
